.main-header{
    /*start header*/
    position: relative;
    .overlay{
        position: absolute;
        top: 0;
        left: 0;
        background-image: linear-gradient(to bottom, rgba(38, 38, 38,0.902) , rgba(0,0,0,0));
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        z-index: 4;
        .titel{
            h1{
                font-size: 42.05px;
                font-family: "GESS-BOld";
                color: rgb(255, 255, 255);
                font-weight: bold;
                text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
                margin-top: 0;
                @media (max-width:425px) {
                    font-size: 35px;
                }
            }
            p{
                font-size: 35.04px;
                color: #fff;
                font-family: "GESS-LIGHT";
                @media (max-width:425px) {
                    font-size: 19px
                }

            }
        }
    }
    .backe-container{
        overflow: hidden;
        img{
            width: 100%;
            @media (max-width:425px) {
                width: auto;
                object-fit: none;
            }
    
            height: 794px;
            @media (max-width:991px) {
                height: auto;
            }
            @media (max-width:425px) {
                height: 794px;
            }

        }
        .chef{
            position: absolute;
            left: 0;
            top: 0;
            width: auto; 
            height: auto;
            z-index: 1;
            @media (max-width:991px) {
                height: 100%;
                width: auto;
            }
            @media (max-width:425px) {
                display: none;
            }

        }
        .pattern{
            position: absolute;
            bottom: 0;
            right: 0;
            width: auto;
            height: auto;
            z-index: 0;
            opacity: .15;        
        }
    }
    &::before{
        content: '';
        position: absolute;
        bottom: -1px;
        left: 0;
        width: 100%;
        height: 90px;
        background-color: #fff;
        clip-path: polygon(0 0%, 0% 100%, 110% 100%);
        z-index: 5;
    }
    /*start navbar*/
    .search-overlay{
        position: fixed;
        width: 100%;
        height: 100%;
        background-color:rgba(31, 31, 31,0.98);
        z-index: 100;
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0;
        visibility: hidden;
        transition: all .5s;

        .form{
            width: max-content;
            position: relative;
            margin: auto;
            @media (max-width:991px) {
                width: 100%;
                
            }
            @media (max-width:425px) {
                width: 300px;
                
            }
        }
        .search-input{
            border: none;
            border-bottom: 1px solid $color-dark;
            width: 750px;
            @media (max-width:991px) {
                width: 100%;       
            }
            padding:20px 40px;
            background-color: transparent;
            outline: none;
            color: #7d7d7d;
            transition: all .5s;
            &:focus{
                border-bottom: 1px solid $maincolor;
            }
        }
        button{
            background-color: transparent;
            border: none;
            outline: none;
            position: absolute;
            top: 50%;
            left: 10px;
            transform: translateY(-50%);
            img{
                filter:  brightness(0) invert(1);
                transition: all .5s ;
                &:hover{
                    filter:  brightness(1) invert(0);
                }
            }
        }
        &.active{
            opacity: 1;
            visibility: visible;
    
        }
        .collaps{
            height: 16px;
            width: 24px;
            cursor: pointer;
            margin:  0 30px;
            z-index: 999;
            position: absolute;
            top: 10%;
            left: 0;
            span{
                background-color: #fff;
                display: block;
                width: 100%;
                height:2px;
                margin: 5px 0;
                transition: all .5s;
                backface-visibility: hidden;
                &:nth-child(1) {
                    transform:rotate(45deg) translate(6px, 7px);
                }
                &:nth-child(2) {
                    transform: rotate(-45deg) translate(-2px, 1px);
                }
            }
            &:hover{
                span{
                    background-color: $maincolor;
                }
            }
         }
    }
    .nav-overlay{
        position: fixed;
        width: 100%;
        height: 100%;
        background-color:rgba(31, 31, 31,0.98);
        z-index: 100;
        display: flex;
        align-items: center;
        opacity: 0;
        visibility: hidden;
        transition: all .5s;
        &.active{
            opacity: 1;
            visibility: visible;
        }
        .collaps{
            height: 16px;
            width: 24px;
            cursor: pointer;
            margin:  0 30px;
            z-index: 999;
            position: absolute;
            top: 10%;
            left: 0;
            span{
                background-color: #fff;
                display: block;
                width: 100%;
                height:2px;
                margin: 5px 0;
                transition: all .5s;
                backface-visibility: hidden;
                &:nth-child(1) {
                    transform:rotate(45deg) translate(6px, 7px);
                }
                &:nth-child(2) {
                    transform: rotate(-45deg) translate(-2px, 1px);
                }
            }
            &:hover{
                span{
                    background-color: $maincolor;
                }
            }
        }
        .links{
            list-style: none;
            margin: 0;
            padding: 0;
            margin-right: 226px;
            @media (max-width:991px) {
                margin-right: 100px;
            }
            @media (max-width:425px) {
                margin-right: 0;
            }
            
            .main-li{
                a{
                    font-size: 24px;
                    color: rgb(255, 255, 255);
                    transition: all .5s;
                    text-decoration: none;
                    @media (max-width:991px) {
                        font-size: 20px;
                        height: min-content;
                    }
                }
                position: relative;
                cursor: pointer;
                display: flex;
                &::before{
                    content: '';
                    position: absolute;
                    right: -100px;
                    top: 50%;
                    background-color: rgba(255, 255, 255,0.102);
                    width: 44px;
                    height: 2px;
                    transition: all .5s;
                }
                position: relative;
                &:not(:last-child){
                    margin-bottom: 18px;
                }
                &.drop{
                    @media (max-width:991px) {
                        flex-direction: column;
                    }
                    &::before{
                        top: 15px;

                    }
                }
                .sup-link{
                    list-style: none;
                    margin: 0;
                    padding: 0;
                    justify-content: center;
                    align-items: center;
                    margin-right: 67px;
                    opacity: 0;
                    visibility: hidden;
                    transition: all .5s;
                    display: flex;
                    @media (max-width:991px) {
                        transition: unset;
                        opacity: 1;
                        visibility: visible;
                        display: none;
                        justify-content: right;
                        align-items: flex-start;
                        flex-direction: column;
    
                    }

                    @media (max-width:991px) {
                        margin:5px 0;
                    }
                    li{
                        margin-right: 30px;
                        @media (max-width:991px) {
                             margin-right: 0;
                             margin-top: 10px;
                            
                        }
                        a{
                            font-size: 20px;
                            color: rgb(134, 133, 133);
                            text-decoration: none;
                            transition: all .5s;
                            @media (max-width:991px) {
                                font-size: 17px;

                            }

                            &:hover{
                                color: $maincolor;
                            }
                              
                        }
                    }
                }
                
                 &:hover{
                     color: $maincolor;
                    &::before{
                        background-color: $maincolor;
                        width: 73px;
                    }
                    .sup-link{
                        opacity: 1;
                        visibility: visible;
                    }
                } 
            }
        }
    }        
    nav{
        position: relative;
        .main-nav{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            display: flex;
            z-index: 5;
            padding: 22px 0;
            align-items: center;
            .search-box{
                margin-right: auto;
                margin-top: 8px;
                cursor: pointer;
                img{
                    filter:  brightness(0) invert(1);
                    transition: all .5s ;
                    &:hover{
                        filter:  brightness(1) invert(0);
                    }
                }
            }
            .lang{
                font-size: 16px;
                margin-top: 10px;        
                a{
                    transition: all 0.5s ;
                    color: #fff;
                    text-decoration: none;
                    transition: all .5s;
                }
                &:hover{
                    a{
                        color: $maincolor;
                    }
                }
            }
            .collaps{
                height: 16px;
                width: 24px;
                cursor: pointer;
                margin:  0 30px;
                @media (max-width:375px) {
                    margin:0 20px;
                }
    
                z-index: 999;
                span{
                    text-align: left;
                    background-color: #fff;
                    display: block;
                    width: 100%;
                    height:2px;
                    margin: 5px 0;
                    transition: all .5s;
                    backface-visibility: hidden;
                    &:nth-child(2){
                        width: 14PX;
                        margin-right: auto;
                    }
                }
                &.on {
                    span{
                        &:nth-child(1) {
                            transform:rotate(45deg) translate(6px, 8px);
                        }
                        &:nth-child(2) {
                            opacity: 0;
                        }
                        &:nth-child(3) {
                            transform: rotate(-45deg) translate(2px, -4px);
                        }
                    }
                }
                &:hover{
                    span:nth-child(2){
                        width: 100%;
                    }
                }
            }
        }
    }
}
/*section aboutUs*/
.about-section{
    padding: 50px 0;
    @media (max-width:991px) {
        padding: 30px 0;
    }
    .about-content{
        @media (max-width:991px) {
            display: flex;
            flex-direction: column;
            text-align: center;
            justify-content: center;
            align-items: center;
            margin-bottom: 30px;
        }
        h2{
            font-size: 25.587px;
            font-family:"GESS-BOld";
            font-weight: bold;
            color: rgb(31, 31, 31);
            margin-bottom: 26px;
        }
        p{
            font-size: 17px;
            color: #7d7d7d;
            font-family: "GESS-LIGHT";
            margin-bottom: 24px;
            padding-left: 40px;
            @media (max-width:991px) {
                padding-left: 0;
            }
        }  
    }
    .about-icon{
        display: flex;
        @media (max-width:991px) {
            text-align: center;
            justify-content: center;
            align-items: center;
            flex-direction: column;
        }

        &:not(:last-child){
            margin-bottom: 30px;
        }
        .img-box{
            display: flex;
            img{
                margin: auto;
                width: auto;
                height: auto;
            }
        }
        .content{
            display: flex;
            flex-direction: column;
            justify-content: center;
            margin-right: 37px;
            @media (max-width:991px) {
                margin-right: 0;
            }

            p{
                font-size: 40px;
                font-family: "MontserratBold";
                color:rgb(255, 136, 60);
                font-weight: bold;                  
                margin: 0;  
            }
            span{
                font-size: 22px;
                color: rgb(31, 31, 31);

            }
        }
    }
}
/*section our services*/
.services{
    padding: 150px 0 130px;
    @media (max-width:991px) {
        padding: 110px 0 100px;
    }

    background-image: linear-gradient(rgba(38, 38, 38, 0.749)) , url('../images/services.jpg');
    background-size: cover;
    background-attachment: fixed;
    position: relative;
    &::before{
        content: '';
        position: absolute;
        bottom: -1px;
        left: 0;
        width: 100%;
        height: 90px;
        background-color: #fff;
        clip-path: polygon(100% 0%, -10% 100%, 100% 100%);
        z-index: 3;
    }
    &::after{
        content: '';
        position: absolute;
        top: -1px;
        left: 0;
        width: 100%;
        height: 90px;
        background-color: #fff;
        clip-path: polygon(100% 0%, -10% 100%, 100% 100%);
        transform: rotate(180deg);
        z-index: 3;
    }
    h2{
        color: #fff;
        text-align: center;
        font-size: 24px;
        position: relative;
        margin-top: 0;
        &::before{
            content: '';
            position: absolute;
            left: 50%;
            bottom: -28px;
            transform: translateX(-50%);  
            display: block;
            background-color: rgb(255, 136, 60);
            width: 49px;
            height: 1px;
        }
    }
    
    .services-containr{
        color: #fff;
        margin-top: 64px;
        .services-icon{
            display: flex;
            margin-bottom: 60px;
            align-items: center;
            @media (max-width:991px) {
                flex-direction: column;
                justify-content: center;
                text-align: center;
                align-items: center;
                margin-bottom: 0;

            }
            img{
                filter:  brightness(0) invert(1);
                transition: all .5s ;
                @media (max-width:991px) {
                    margin: auto;
                    display: block;
                    width: auto;
                    height: auto;
                }

            }
            .img-box{
                @media (max-width:991px) {
                    margin-bottom: 20px;
                    width: 100%
                }
                display: flex;
                img{
                    display: block;
                    margin: auto;
                }
            }
            .content{
                margin-right: 30px;
                @media (max-width:991px) {
                    margin-right: 0;
                }

                p{
                    font-size: 18px;
                    margin: 0;
                    transition: all .5s ;
                }
            }
            &:hover{
                .content{
                    p{
                        color: $maincolor;
                    }
                }            
                img{
                    filter:  brightness(1) invert(0);
    
                }
            }
    
        }

    }
    .row{
        @media (max-width:991px) {
            margin-right: 0;
            margin-left: 0;
        
        }
    }
    .owl-theme .owl-dots .owl-dot.active span, .owl-theme .owl-dots .owl-dot:hover span{
        background: $maincolor;

    }
    .owl-dot{
        outline: none;
    }
    .owl-item{
        margin-left: 10px !important;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .owl-stage{
        display: flex;
        justify-content: center;
        align-items: center;
    }

}
/*start why Us*/
.why{
    padding: 70px 0 100px;
    @media (max-width:991px) {
        padding: 50px 0 ;
    }
    h2{
        color: rgb(31, 31, 31);
        text-align: center;
        font-size: 24px;
        position: relative;
        margin-top: 0;
        &::before{
            content: '';
            position: absolute;
            left: 50%;
            bottom: -28px;
            transform: translateX(-50%);  
            display: block;
            background-color: rgb(255, 136, 60);
            width: 49px;
            height: 1px;
        }
    }

    .why-containr{
        margin-top: 102px;
        @media (max-width:991px) {
            margin-top: 50px;
        }
        display: flex;
        justify-content: center;
        align-items: center;
        .why-box{
            position: relative;
            box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.08);
            width: 165px;
            height: 130px;
            &:not(:first-child){
                margin-right: 30px;
            }
            padding: 60px 10px 15px;
            @media (max-width:1024px) {
                padding: 60px 5px 15px;
            }
            text-align: center;
            .icon-box{
                border-radius: 50%;
                background-color: rgb(143, 143, 143);
                width: 79px;
                height: 79px;
                position: absolute;
                top: 0;
                left: 50%;
                transform: translate(-50%,-50%);
                display: flex;
                align-items: center;
                justify-content: center;
                transition: all .5s;
                box-shadow: 0 0 0 0 rgba($maincolor, 1);
                img{
                    display: block;
                    margin: auto;
                    width: auto;
                }
            }
            p{
                font-size: 15px;
                font-family: "GESS-LIGHT";
                font-weight: 100;
                color: rgb(80, 80, 80);
                margin: 0;
                transition: all .5s;
                  
            }
            &:hover{
                .icon-box{
                    background-color: $maincolor;
                    animation: heart .8s  cubic-bezier(.66, 0, 0, 1) infinite;
                } 
                p{
                    color: $maincolor;
                }
                a{

                    text-decoration: none;
                }
            }
        }
    }
    .owl-item{
        margin-left: 10px !important;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .owl-stage{
        display: flex;
        justify-content: center;
        align-items: center;
        .why-box{
            margin-top: 50px;
            margin-bottom: 50px;
        }
    }
    .owl-theme .owl-nav.disabled+.owl-dots{
        position: absolute;
        bottom: 0;
    }
    .owl-dot{
        outline: none;
    }
    .owl-theme .owl-dots .owl-dot.active span, .owl-theme .owl-dots .owl-dot:hover span{
        background: $maincolor;

    }
}
/*start faci*/
.faci{
    padding: 170px 0 146px;
    overflow: hidden;
    background-image: linear-gradient(to right, $maincolor 55%, transparent), 
                        url('../images/faci.jpg');
    @media (min-width:2560px) {
        background-image: linear-gradient(to right, $maincolor 73%, transparent), 
        url('../images/faci.jpg');

    }
    @media (max-width:425px) {
        background-image: linear-gradient($maincolor);

    }

    background-repeat: no-repeat;
    background-position-x: right;
    display: flex;
    position: relative;
    background-size: contain;
    @media (max-width:425px) {
        background-size: cover;
    }
    .faci-content{  
        margin-right: auto;
        width: 50%;
        @media (max-width:991px) {
            width: 60%;
        }
        @media (max-width:425px) {
            width: 100%;
            text-align: center;
        }
    
    }
    h2{
        font-size: 25.587px;
        color: rgb(255, 255, 255);
        font-weight: bold;
        font-family: "GESS-BOld";
        margin-bottom: 25px;
        margin-top: 0;
    }
    p{
        font-family: "GESS-LIGHT";
        font-size: 17px;
        color: rgb(255, 255, 255);
    }
    &::before{
        content: '';
        position: absolute;
        bottom:-1px;
        left: 0;
        width: 100%;
        height: 90px;
        background-color: #fff;
        clip-path: polygon(0 0%, 0% 100%, 110% 100%);
        z-index: 3;
        display: block;
    }
    &::after{
        content: '';
        position: absolute;
        top: -1px;
        left: 0;
        width: 100%;
        height: 90px;
        background-color: #fff;
        transform: rotate(180deg);
        clip-path: polygon(0 0%, 0% 100%, 110% 100%);
        z-index: 3;
        display: block;
    }
    .pattern{
        position: absolute;
        top: -20px;
        left: 0;
        width: auto;
        height: auto;
        @media (max-width:425px) {
            left: -126px;
        }
    }
}
/*start client*/
.client{
    padding: 90px 0 ;
    @media (max-width:991px) {
        padding: 0;
        text-align: center;
        
    }

    .client-content{
        margin-top: 70px;
        @media (max-width:991px) {
            margin-top: 0;

        }
        h2{
            margin-bottom: 28px;
            font-size: 25.587px;
            color: rgb(31, 31, 31);
            font-weight: bold;
            font-family: "GESS-BOld";
        }
        p{
            font-size: 17px;
            font-family: "GESS-LIGHT";
            color: rgb(104, 104, 104);
            margin-bottom: 44px;
            @media (max-width:991px) {
                margin-bottom: 14px;
            
            }

        }
        @media (max-width:991px) {
            margin-bottom: 20px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
        .owl-item{
            display: flex;
            justify-content: center;
            align-items: center;
            text-align: center;
            height: 150px;
        }
        .owl-theme .owl-dots .owl-dot.active span, .owl-theme .owl-dots .owl-dot:hover span{
            background: $maincolor;
    
        }    
        .client-logo{
            margin-bottom: 20px;
            display: none;
            @media (max-width:991px) {
                display: block;
            }
            .logo-box{
                a{
                    text-decoration: none;
                }
            }
            img{
                filter: grayscale(.8);
                opacity: .4;
                transition: .5s;
                width: auto;
                height: auto;
                display: block;
            }

        }
    }
    .clients .client-logo{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        .logo-box{
            width: 30%;
            height: 200px;
            display: flex;
            justify-content: center;
            align-items: center;

            img{
                filter: grayscale(.8);
                opacity: .4;
                transition: .5s;
            }
            &:hover{
                img{
                    filter: none;
                    opacity: 1;
                }
            }
            &:nth-child(2),
            &:nth-child(5){
                border-right: 1px solid rgb(242, 242, 242);
                border-left: 1px solid rgb(242, 242, 242);
            }
            &:nth-child(4),
            &:nth-child(6),
            &:nth-child(5){
                border-top: 1px solid rgb(242, 242, 242);
            }
        }
    }
    .main{
        @media (max-width:991px) {
            display: none
            
        }
    }
}
