/*start footer*/
.main-footer{
    background-color: #1f1f1f;
    padding: 50px 0;
    @media (max-width:991px) {
        padding: 30px 0 10px;
    
    }
    position: relative;
    .logo-box{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 47px;
        img{
            display: block;
            width: auto;
            height: auto;
        }
    }
    .right{
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        border-left: 1px solid  rgb(43, 43, 43);
        @media (max-width:425px) {
            border-left: none;
        }

        .content{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin-top: auto;
            text-align: center;
        }
        h2{
            font-size: 18px;
            color: rgb(255, 255, 255);
            margin-bottom: 25px;
        }
        p{
            font-size: 16px;
            color: rgb(139, 138, 138);
            span{
                display: block;
            }
        }
    }
    .center{
        text-align: center;
        @media (max-width:425px) {
            display: none;
        }
        h2{
            font-size: 18px;
            color: rgb(255, 255, 255);
            margin-bottom: 25px;
        }
        .social{
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 64px;
            @media (max-width:425px) {
                margin-bottom: 34px;
            }
    
            a{
                &:not(:first-child){
                    margin-right: 6px;
                }
                text-decoration: none;

            }
            .icon{
                border-radius: 50%;
                background-color: #262626;
                width: 44px;
                height: 44px;
                display: flex;
                justify-content: center;
                align-items: center;
                position: relative;
                overflow: hidden;
                i{
                    font-size: 16px;
                    color: rgb(255, 255, 255);
                    z-index: 2;
                }
                &::before,
                &::after{
                    content: '';
                    display: block;
                    width: 109%;
                    height: 100%;
                    background-color: #f68545;
                    position: absolute;
                    z-index: 1;
                    transition: all .5s;
                }
                &::before{
                    right: 100%;
                    clip-path: polygon(0 0, 0% 100%, 100% 0);
            
                }
                &::after{
                    left: 100%;
                    clip-path: polygon(100% 3%, 0% 100%, 100% 100%);
                }
                &:hover{

                    &::before{
                        right: -4px;
                    }
                    &::after{
                        left: -2px;
                    }
                
                }
            }
        }
        .copy{
            font-size: 13px;
            color: rgb(139, 138, 138);
            img{ 
                margin-right: 3px;
            }             
        }
    }
    .left{
        text-align: center;
        border-right: 1px solid  rgb(43, 43, 43);
        @media (max-width:425px) {
            border-right: none;
        }

        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        flex-direction: column;
        h2{
            font-size: 18px;
            color: rgb(255, 255, 255);
            margin-bottom: 25px;
        }
        p{
            font-size: 16px;
            color: rgb(139, 138, 138);
            font-family: "MontserratRegular";
            span{
                display: block;
            }
        }

    }
    .pattern{
        position: absolute;
        bottom: 0;
        right: 0;
        width: auto;
        height: auto;
        z-index: 0;
        opacity: .02;
    }
    .bottom{
        display: none;
        @media (max-width:425px) {
            display: block;
        }

    }
    
}