body{
    direction: rtl;
    font-family: 'GESS', sans-serif;
    &.overflowHidden{
        overflow: hidden;
    }
}
*{
    font-family: inherit;
}
.main-btn{
    font-size: 16px;
    color: rgb(170, 170, 170);
    border:2px solid  rgb(233, 233, 233);
    width: 165px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    transition: all .5s;    
    overflow: hidden;
    span{
        z-index: 2;
    }
    &::before,
    &::after{
        content: '';
        display: block;
        width: 106%;
        height: 100%;
        background-color: #f68545;
        position: absolute;
        z-index: 1;
        transition: all .5s;
    }
    &::before{
        right: 100%;
        clip-path: polygon(0 0, 0% 100%, 100% 0);

    }
    &::after{
        left: 100%;
        clip-path: polygon(100% 3%, 0% 100%, 100% 100%);
    }
    &:hover{
        text-decoration: none;
        color: #fff;
        border:2px solid  #f68545;

        &::before{
            right:-8px;
        }
        &::after{
            left: -8px;
        }
    
    }
}
// animation
@keyframes heart
{
    to
    {
        box-shadow: 0 0 0 8px rgba(#f68545, 0);
    }
}

@import 'import';

